import PropTypes from 'prop-types';
import { useEffect, useState } from 'react';
import useBase from 'hooks/collage/base';
import { useNavigate } from 'react-router-dom';
import constants from 'static/config/collage';
import HandleError from 'hocs/handle-error';
import Box from '@mui/material/Box';

import Collage from 'components/collage/Collage';
import DownloadDialog from 'components/collage/DownloadDialog';
import SaveDialog from 'components/collage/SaveDialog';
import SaveStatusDialog from 'components/collage/SaveStatusDialog';
import Header from 'components/collage/Header';
import Loading from 'components/Loading';
import View from 'components/collage/View';

import DesktopActions from 'components/collage/DesktopActions';
import DesktopControls from 'components/collage/DesktopControls';
import DesktopImageOptions from 'components/collage/DesktopImageOptions';
import EditControl from 'components/collage/EditControl';
import MobileActions from 'components/collage/MobileActions';
import MobileControls from 'components/collage/MobileControls';
import CollageMetaInfo from 'components/collage/CollageMetaInfo';
import PaddingControl from 'components/collage/PaddingControl';
import RadiusControl from 'components/collage/RadiusControl';
import TextControl from 'components/collage/TextControl';
import ColorControl, { ColorPicker } from 'components/collage/ColorControl';
import CommonDialog from 'components/collage/CommonDialog';

export default function BaseCollage({ getCollage, clearCollage, saveType }) {
  const { paperSizes } = constants;
  const navigate = useNavigate();
  const [details, setDetails] = useState({});
  const [status, setStatus] = useState('fetching');
  const [commonOpen, setCommonOpen] = useState(false);
  const [commonMessage, setCommonMessage] = useState('');
  const [commonProgress, setCommonProgress] = useState(false);
  const [nameError, setNameError] = useState('');
  const [textError, setTextError] = useState('');

  console.log('details', details);

  const [
    collageType,
    ,
    collageName,
    ,
    ,
    dimensionProps,
    optionProps,
    actionProps,
    imageProps,
    treeProps,
    handleClear,
    collageCallback,
    handleRemoveImage,
    newCollage,
    saveStatus,
    saveCollage,
    handleCollageTypeChange,
    handleNameChange,
  ] = useBase(details);

  const openCommonDialog = (message, progress) => {
    if (message !== '') {
      setCommonMessage(message);
      setCommonProgress(progress);
      setCommonOpen(true);
    }
  };

  const closeCommonDialog = () => {
    if (imageProps.imageCountMinError !== '') {
      handleClear();
      imageProps.setImageCountMinError('');
    }
    if (imageProps.imageCountMaxError !== '') {
      imageProps.setImageCountMaxError('');
    }
    setCommonProgress(false);
    setCommonMessage('');
    setCommonOpen(false);
  };

  useEffect(
    () => imageProps.handleImageCount(imageProps.imgCount),
    [imageProps.imgCount]
  );

  useEffect(() => {
    if (imageProps.imageCountMinError !== '') {
      openCommonDialog(imageProps.imageCountMinError, false);
    }
    if (imageProps.imageCountMinError !== '') {
      openCommonDialog(imageProps.imageCountMaxError, false);
    }
  }, [imageProps.imageCountMinError, imageProps.imageCountMaxError]);

  if (
    imageProps.imgCount === constants.minImages &&
    actionProps.removeImage === true
  ) {
    actionProps.setRemoveImage(false);
    openCommonDialog(
      `Need atleast ${constants.minImages} images for collage`,
      false
    );
  }

  const errors = [
    nameError,
    textError,
    imageProps.imageCountMaxError,
    imageProps.imageCountMinError,
  ].filter((el) => el !== '');

  const {
    options,
    handlePaddingChange,
    handleRadiusChange,
    handleColorClose,
    handleColorClick,
    handleColorChange,
    handleTextChange,
    handlePositionChange,
    displayColorPicker,
    editControl,
    setEditControl,
    editOptionsValue,
    setEditOptionsValue,
    handleTextSubmit,
  } = optionProps;

  useEffect(async () => {
    if (Object.keys(details).length === 0) {
      if (saveType === 'existing') {
        const collageDetails = await getCollage(
          imageProps.setImageCount,
          imageProps.incrementNewImageCount
        );
        setDetails(collageDetails);
      }
      setStatus('fetch end');
    }
  }, []);

  useEffect(() => {
    let st = status;
    if (st === 'fetching' && saveType === 'new') st = 'empty';
    if (imageProps.imgCount === 0 && st === 'ready') {
      st = 'empty';
    }
    if (
      imageProps.imgCount === 0 &&
      Object.keys(details).length === 0 &&
      st === 'fetch end'
    ) {
      st = 'empty';
    }
    if (
      imageProps.images.length > 0 &&
      imageProps.images.length === imageProps.imgCount
    ) {
      st = 'preparing';
    }
    if (imageProps.loaded >= imageProps.imgCount && imageProps.imgCount > 0) {
      st = 'ready';
    }
    if (
      imageProps.imgCount >= constants.minImages &&
      imageProps.images.length < imageProps.imgCount
    ) {
      st = 'loading';
    }
    if (imageProps.imageCountMinError !== '') {
      st = 'empty';
      openCommonDialog(imageProps.imageCountMinError, false);
    }
    if (imageProps.imageCountMaxError !== '') {
      if (imageProps.imgCount === 0) {
        st = 'empty';
      } else {
        st = 'ready';
      }
      openCommonDialog(imageProps.imageCountMaxError, false);
    }
    setStatus(st);
  }, [imageProps]);

  const clearAll = async () => {
    openCommonDialog('Deleting Collage', true);
    setDetails({});
    handleClear();
    await clearCollage();
    setStatus('fetch end');
    closeCommonDialog();
    if (saveType === 'existing') {
      openCommonDialog('Navigating to collages page', true);
      setTimeout(() => {
        closeCommonDialog();
        navigate('/collages');
      }, 3000);
    }
  };

  const handleSave = async (name) => {
    actionProps.onSaveClick();
    const res =
      saveType === 'new' ? await newCollage(name) : await saveCollage(name);
    if (res) {
      setTimeout(() => {
        if (saveType === 'new') clearAll();
        actionProps.setSaveStart(false);
        navigate('/collages');
      }, 3000);
    }
  };

  const downloadDialog = (
    <HandleError errors={errors} handleClose={actionProps.onDownloadClose}>
      <DownloadDialog
        open={actionProps.open}
        onClick={actionProps.onDownloadClick}
        onClose={actionProps.onDownloadClose}
        sizes={paperSizes}
      />
    </HandleError>
  );

  const saveDialog = (
    <HandleError errors={errors} handleClose={actionProps.onSaveClose}>
      <SaveDialog
        collageName={collageName}
        open={actionProps.saveOpen}
        onClick={handleSave}
        onClose={actionProps.onSaveClose}
      />
    </HandleError>
  );

  const saveStatusDialog = (
    <SaveStatusDialog open={actionProps.saveStart} status={saveStatus} />
  );

  const collage = (
    <Collage
      dimensionProps={dimensionProps}
      imageProps={imageProps}
      actionProps={actionProps}
      collageCallback={collageCallback}
      handleRemoveImage={handleRemoveImage}
      options={optionProps.options}
      tree={treeProps.tree}
    />
  );

  const paddingControl = (
    <PaddingControl
      padding={options.padding}
      handlePaddingChange={handlePaddingChange}
    />
  );

  const radiusControl = (
    <RadiusControl
      radius={options.borderRadius}
      handleRadiusChange={handleRadiusChange}
    />
  );

  const colorControl = (
    <ColorPicker
      open={displayColorPicker}
      color={options.color}
      handleChange={handleColorChange}
      handleClose={() => {
        setEditControl(null);
        handleColorClose();
      }}
    />
  );

  const desktopColorControl = (
    <ColorControl
      color={options.color}
      displayColorPicker={displayColorPicker}
      handleChange={handleColorChange}
      handleClose={handleColorClose}
      handleClick={handleColorClick}
    />
  );

  const textControl = (
    <TextControl
      text={options.text}
      handleTextChange={handleTextChange}
      topText={options.topText}
      handlePositionChange={handlePositionChange}
      handleTextSubmit={handleTextSubmit}
      textError={textError}
      handleTextError={setTextError}
    />
  );

  const mobileControls = (
    <MobileControls
      optionProps={optionProps}
      actionProps={actionProps}
      dimensionProps={dimensionProps}
      handleClear={clearAll}
      saveType={saveType}
      paddingControl={paddingControl}
      radiusControl={radiusControl}
      colorControl={colorControl}
      textControl={textControl}
      editControl={editControl}
      handleEditControl={setEditControl}
      editOptionsValue={editOptionsValue}
      handleEditOptionsValue={setEditOptionsValue}
    />
  );

  const header = (
    <Header
      headerRef={dimensionProps.headerRef}
      name={collageName}
      handleNameChange={handleNameChange}
      nameError={nameError}
      handleNameError={setNameError}
    />
  );

  const desktopPhotosComp = (
    <DesktopImageOptions
      onDelete={actionProps.onDelete}
      onAdd={dimensionProps.handleImageAddClick}
      clearDelete={actionProps.clearDelete}
    />
  );

  const desktopEditComp = (
    <EditControl
      paddingControl={paddingControl}
      radiusControl={radiusControl}
      colorControl={desktopColorControl}
      textControl={textControl}
    />
  );

  const desktopControls = (
    <DesktopControls
      innerRef={dimensionProps.footerRef}
      onRefresh={actionProps.onRefresh}
      photosComp={desktopPhotosComp}
      editComp={desktopEditComp}
      handleClear={clearAll}
      clearDelete={actionProps.clearDelete}
      saveType={saveType}
    />
  );

  const isMobile = window.innerWidth < window.innerHeight;
  const controls = isMobile ? mobileControls : desktopControls;

  const onSave = async () => actionProps.handleSave();

  const actionsComp = isMobile ? (
    <MobileActions
      actionsRef={dimensionProps.actionsRef}
      onDownload={actionProps.handleDownload}
      onSave={onSave}
    />
  ) : (
    <DesktopActions
      actionsRef={dimensionProps.actionsRef}
      onDownload={actionProps.handleDownload}
      onSave={onSave}
    />
  );

  const collageMetaInfo = (
    <CollageMetaInfo
      collageMetaRef={dimensionProps.collageMetaRef}
      collageType={collageType}
      width={dimensionProps.width}
      sizes={paperSizes}
      handleTypeChange={handleCollageTypeChange}
      imageCount={imageProps.imgCount}
    />
  );

  return (
    <Box
      sx={{
        backgroundColor: 'gray',
        height: Math.min(window.innerHeight, window.screen.availHeight),
        overflow: 'hidden',
      }}
    >
      <View
        open={actionProps.open}
        saveOpen={actionProps.saveOpen}
        saveStatusOpen={actionProps.saveStart}
        dimensionProps={dimensionProps}
        imageProps={imageProps}
        header={header}
        actions={actionsComp}
        downloadDialog={downloadDialog}
        saveDialog={saveDialog}
        saveStatusDialog={saveStatusDialog}
        collage={collage}
        controls={controls}
        status={status}
        saveType={saveType}
        collageMetaInfo={collageMetaInfo}
      />
      <input
        ref={dimensionProps.addImageRef}
        type="file"
        hidden
        multiple
        accept="image/*"
        onChange={imageProps.handleLoad}
      />
      {actionProps.generate ? <Loading title="Creating collage ..." /> : null}
      <CommonDialog
        open={commonOpen}
        message={commonMessage}
        progress={commonProgress}
        onClose={closeCommonDialog}
      />
    </Box>
  );
}

BaseCollage.defaultProps = {
  getCollage: () => {},
  clearCollage: () => {},
};

BaseCollage.propTypes = {
  getCollage: PropTypes.func,
  clearCollage: PropTypes.func,
  saveType: PropTypes.string.isRequired,
};
