import { useState, useEffect } from 'react';
import constants from 'static/config/collage';
import helpers from './helpers';

export default function Collage({
  dimensionProps,
  imageProps,
  actionProps,
  collageCallback,
  handleRemoveImage,
  options,
  tree,
}) {
  const [collage, setCollage] = useState(null);
  const { images, setLoaded } = imageProps;
  const { generate, handleGenerateCallback, printOptions, removeImage } =
    actionProps;
  const { height, width, imageHeight, imageWidth } = dimensionProps;
  const { fontSize, lineHeight } = constants;

  const handleCollageImageLoad = () => setLoaded((cnt) => cnt + 1);
  const isEmpty = (t) => Object.keys(t).length === 0;
  const createCollage = () => {
    console.log('started collage loading', tree);
    tree.addSize(imageWidth, imageHeight, tree.root.value.aspectRatio);
    console.log('collage added size');
    tree.addOptions(options);
    console.log('collage added options', images.length);
    if (images.length > 0) {
      tree.addImageData(images);
      console.log('collage added image data');
      const collageElem = tree.DOMDisplay(
        width,
        height,
        removeImage,
        handleRemoveImage,
        handleCollageImageLoad
      );
      console.log('collage added dom display');
      setCollage(collageElem);
      collageCallback(tree);
    }
  };

  useEffect(() => {
    if (!isEmpty(tree) && tree.root.left != null) {
      console.log('collage, tree is not empty');
      createCollage();
    }
  }, []);

  useEffect(() => {
    if (!isEmpty(tree) && tree.root.left != null) {
      console.log('collage, tree is not empty 1');
      createCollage();
    } else {
      setCollage(null);
    }
  }, [tree, options, imageWidth, imageHeight, removeImage, images]);

  const downloadCollage = (canvas, pW, pH) => {
    canvas.toBlob((blob) => {
      const link = document.createElement('a');
      link.download = `collage-${pW.toFixed(2)}X${pH.toFixed(
        2
      )}-${Date.now()}.jpg`;
      link.href = URL.createObjectURL(blob);
      link.click();
      URL.revokeObjectURL(link.href);
      link.href = null;
      handleGenerateCallback();
    }, 'image/jpeg');
  };

  const getCanvasTree = () => {
    const canvasTree = tree.copy();
    canvasTree.replaceImages(images);
    return canvasTree;
  };

  const getCanvas = (pw, ph) => {
    const canvasElement = document.createElement('canvas');
    canvasElement.width = pw;
    canvasElement.height = ph;
    return canvasElement;
  };

  const generateCollageTree = (pw, ph) => {
    const canvasTree = getCanvasTree();
    const ar = tree.root.value.aspectRatio;
    const scaleFactor = pw / width;
    const [imgPW, imgPH] = helpers.getImageDimensions(
      pw,
      ph,
      options.text,
      0,
      0,
      fontSize,
      lineHeight,
      scaleFactor,
      tree.root.value.aspectRatio
    );
    canvasTree.addSize(imgPW, imgPH, ar);
    return canvasTree;
  };

  const generateCollage = (ctree, pw, ph) => {
    const scaleFactor = pw / width;
    const canvasElement = getCanvas(pw, ph);
    ctree.CanvasDisplay(pw, ph, canvasElement, scaleFactor);

    setTimeout(() => downloadCollage(canvasElement, pw, ph), '5000');
  };

  useEffect(() => {
    if (generate === true) {
      const { paperWidth: pw, paperHeight: ph } = printOptions;
      const canvasTree = generateCollageTree(pw, ph);
      const [valid, probNodes] = canvasTree.validateAddSize();
      if (valid) {
        generateCollage(canvasTree, pw, ph);
      } else {
        const [newPW, newPH] = helpers.optimizeDimensions(pw, ph, probNodes);
        const newCanvasTree = generateCollageTree(newPW, newPH);
        const [newValid, newProbNodes] = newCanvasTree.validateAddSize();
        if (newValid) {
          generateCollage(newCanvasTree, newPW, newPH);
        } else {
          console.log('unable to generate collage', newProbNodes);
        }
      }
    }
  }, [generate]);

  return collage;
}
