import PropTypes from 'prop-types';
// import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import collageBack from 'static/images/collage_back1.webp';

export default function EmptyCollage({ width, height, loadComponent }) {
  console.log(width, height);
  return (
    <Box
      sx={{
        backgroundImage: `url(${collageBack})`,
        backgroundSize: width,
        objectFit: 'fill',
        width,
        height,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
      }}
    >
      {loadComponent}
    </Box>
  );
}

EmptyCollage.propTypes = {
  width: PropTypes.number.isRequired,
  height: PropTypes.number.isRequired,
  loadComponent: PropTypes.node.isRequired,
};
