import PropTypes from 'prop-types';
import Typography from '@mui/material/Typography';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogActions from '@mui/material/DialogActions';
import Button from '@mui/material/Button';
import { alpha } from '@mui/material/styles';

export default function ClearCollageDialog({
  open,
  onSubmit,
  onCancel,
  saveType,
}) {
  return (
    <Dialog open={open}>
      <DialogTitle
        sx={{
          textAlign: 'center',
          fontWeight: 'bold',
          color: alpha('#000', 0.8),
        }}
      >
        {saveType === 'new' ? 'Clear' : 'Delete'} Collage
      </DialogTitle>
      <DialogContent dividers>
        <DialogContentText sx={{ fontWeight: 'bold', color: 'gray', pb: 1 }}>
          Do you want to {saveType === 'new' ? 'clear' : 'delete'} this collage?
        </DialogContentText>
        <Typography
          variant="subtitle2"
          sx={{ color: 'red', fontWeight: 'bold' }}
          noWrap
        >
          Note:
        </Typography>
        <Typography
          sx={{ fontSize: '0.8rem', color: 'gray', fontWeight: 'bold' }}
        >
          This change is not reversible
        </Typography>
      </DialogContent>
      <DialogActions>
        <Button onClick={onSubmit} sx={{ color: 'red' }}>
          {saveType === 'new' ? 'Clear' : 'Delete'}
        </Button>
        <Button variant="outlined" onClick={onCancel}>
          Cancel
        </Button>
      </DialogActions>
    </Dialog>
  );
}

ClearCollageDialog.propTypes = {
  open: PropTypes.bool.isRequired,
  onSubmit: PropTypes.func.isRequired,
  onCancel: PropTypes.func.isRequired,
  saveType: PropTypes.oneOf(['new', 'existing']).isRequired,
};
