import constants from 'static/config/collage';

function textCanvasDraw(
  ctx,
  textArray,
  position,
  scaleFactor,
  canvasWidth,
  textHeight
) {
  const { fontSize, textPadding } = constants;
  ctx.font = `${scaleFactor * fontSize}px Arial`;
  ctx.textAlign = 'center';
  ctx.textBaseline = 'top';
  ctx.fillStyle = '#1c1c1cb3';
  ctx.fillRect(0, position, canvasWidth, position + textHeight);
  ctx.fillStyle = 'white';

  // eslint-disable-next-line no-restricted-syntax
  for (const chunk of textArray) {
    const [textPart, , y] = chunk;
    ctx.fillText(
      textPart,
      canvasWidth / 2,
      y + position + 2.5 + textPadding * scaleFactor
    );
  }
}

function getClosest(arr, index1, index2, target) {
  if (target - arr[index1] >= arr[index2] - target) return index2;
  return index1;
}

function findClosest(arr, target) {
  const n = arr.length;

  if (target <= arr[0]) return 0;
  if (target >= arr[n - 1]) return n - 1;

  let start = 0;
  let end = n;
  let mid = 0;
  while (start < end) {
    mid = (start + end) / 2;

    if (arr[mid] === target) return mid;

    if (target < arr[mid]) {
      if (mid > 0 && target > arr[mid - 1])
        return getClosest(arr, mid - 1, mid, target);
      end = mid;
    } else {
      if (mid < n - 1 && target < arr[mid + 1])
        return getClosest(arr, mid, mid + 1, target);
      start = mid + 1;
    }
  }

  return mid;
}

function getCut() {
  const cutArr = ['v', 'h'];
  return cutArr[Math.round(Math.random())];
}

function findImgPair(arr, target) {
  let start = 0;
  let end = arr.length - 1;
  let i = start;
  let j = end;
  let minMatchingCost = Math.abs(arr[start] + arr[end] - target);
  while (start <= end) {
    if (arr[start] + arr[end] > target) {
      if (Math.abs(arr[start] + arr[end] - target) < minMatchingCost) {
        // update fetched pair and matching cost
        minMatchingCost = Math.abs(arr[start] + arr[end] - target);
        i = start;
        j = end;
      }
      end -= 1;
    } else if (arr[start] + arr[end] <= target) {
      if (Math.abs(arr[start] + arr[end] - target) < minMatchingCost) {
        minMatchingCost = Math.abs(arr[start] + arr[end] - target);
        i = start;
        j = end;
      }
      start += 1;
    } else {
      // best pair (m === 0) found
      i = start;
      j = end;
      break;
    }
  }
  const randomNumber = Math.round(Math.random());
  if (randomNumber) return [i, j];
  return [j, i];
}

function getAspectRatio(cut, ar1, ar2) {
  if (cut === 'v') return ar1 + ar2;
  return (ar1 * ar2) / (ar1 + ar2);
}

function downscaleImage(image, newWidth, newHeight, fileType) {
  const canvas = document.createElement('canvas');
  canvas.width = newWidth;
  canvas.height = newHeight;
  const ctx = canvas.getContext('2d');
  ctx.drawImage(image, 0, 0, newWidth, newHeight);
  return canvas.toDataURL(fileType);
}

