function optimizeImage(image, newWidth, newHeight) {
  const canvas = document.createElement('canvas');
  canvas.width = newWidth;
  canvas.height = newHeight;
  const ctx = canvas.getContext('2d');
  ctx.drawImage(image, 0, 0, newWidth, newHeight);
  const dataURL = canvas.toDataURL('image/webp', 0.3);
  return dataURL;
}

function resizeImages(originalImages, maxWidth, handleImages, addImagesToDB) {
  const tempImages = [];
  originalImages.forEach((origImage) => {
    const image = new Image();
    image.onload = () => {
      const { title, aspectRatio, type } = origImage;
      const dataurl = downscaleImage(
        image,
        maxWidth,
        maxWidth / aspectRatio,
        origImage.type
      );
      const tempImage = {
        title,
        src: dataurl,
        naturalHeight: maxWidth / aspectRatio,
        naturalWidth: maxWidth,
        aspectRatio,
        type,
      };
      tempImages.push(tempImage);
      handleImages((imgs) => {
        const ind = imgs.findIndex((elem) => elem.title === origImage.title);
        return [...imgs.slice(0, ind), tempImage, ...imgs.slice(ind + 1)];
      });
      addImagesToDB(tempImage);
    };
    image.src = origImage.src;
  });
}

function convertBase64(file) {
  return new Promise((resolve, reject) => {
    const fileReader = new FileReader();
    fileReader.readAsDataURL(file);
    fileReader.onload = () => {
      resolve(fileReader.result);
    };
    fileReader.onerror = (error) => {
      reject(error);
    };
  });
}

function convertBase64String(file) {
  return new Promise((resolve, reject) => {
    const fileReader = new FileReader();
    fileReader.readAsD(new Blob(file));
    fileReader.onload = () => {
      resolve(fileReader.result);
    };
    fileReader.onerror = (error) => {
      reject(error);
    };
  });
}

async function handleImageLoad(
  event,
  handleImageCount,
  handleNewImageCount,
  handleImageData
) {
  if (event.target.files) {
    handleImageCount((cnt) => cnt + event.target.files.length);
    handleNewImageCount(event.target.files.length);
    const files = Array.from(event.target.files);
    files.map(async (file) => {
      const image = new Image();
      image.onload = (e) => {
        const { naturalWidth, naturalHeight, src } = e.target;
        const title = `${Date.now()}-${file.name}`;
        const aspectRatio = naturalWidth / naturalHeight;
        const imageDetail = {
          title,
          src,
          naturalHeight,
          naturalWidth,
          aspectRatio,
          targetAspectRatio: aspectRatio,
          type: file.type,
          file,
        };
        handleImageData(imageDetail);
      };
      image.src = URL.createObjectURL(file);
    });
    files.map((file) => URL.revokeObjectURL(file)); // avoid memory leak
  }
}

function getCollageDimension(maxW, maxH, a) {
  const origAR = maxW / maxH;
  if (origAR < a) return [maxW, maxW / a];
  if (origAR > a) return [maxH * a, maxH];
  return [maxW, maxH];
}

function wrapText(text, x, y, maxWidth, fontSize, lineHeight) {
  // First, start by splitting all of our text into words,
  // but splitting it into an array split by spaces
  if (text.length === 0) return [];
  const paddingX = 1; // 8px on both sided
  const textCanvas = document.createElement('canvas');
  textCanvas.width = maxWidth - 2 * paddingX * 8;
  textCanvas.height = 1000;
  const ctx = textCanvas.getContext('2d');
  ctx.font = `${fontSize}px Arial`;
  ctx.textAlign = 'center';
  const tempX = x;
  let tempY = y;
  const words = text.split(' ');
  let line = ''; // This will store the text of the current line
  let testLine = ''; // This will store the text when we add a word, to test if it's too long
  const lineArray = []; // This is an array of lines, which the function will return

  // Lets iterate over each word
  for (let n = 0; n < words.length; n += 1) {
    // Create a test line, and measure it..
    testLine += `${words[n]} `;
    const metrics = ctx.measureText(testLine);
    const testWidth = metrics.width;
    // If the width of this test line is more than the max width
    if (testWidth > textCanvas.width && n > 0) {
      // Then the line is finished, push the current line into "lineArray"
      lineArray.push([line, tempX, tempY]);
      // Increase the line height, so a new line is started
      tempY += lineHeight;
      // Update line and test line to use this word as the first word on the next line
      line = `${words[n]} `;
      testLine = `${words[n]} `;
    } else {
      // If the test line is still less than the max width, then add the word to the current line
      line += `${words[n]} `;
    }
    // If we never reach the full max width, then there is only one line..
    // so push it into the lineArray so we return something
    if (n === words.length - 1) {
      lineArray.push([line, tempX, tempY]);
    }
  }
  // Return the line array
  return lineArray;
}

function getTextHeight(
  text,
  x,
  y,
  maxWidth,
  fontSize,
  lineHeight,
  scaleFactor
) {
  const { textPadding } = constants;
  const lineArray = wrapText(
    text,
    x,
    y,
    maxWidth,
    fontSize * scaleFactor,
    lineHeight * scaleFactor
  );
  return (lineArray.length * lineHeight + 2 * textPadding) * scaleFactor;
}

function getImageDimensions(
  paperWidth,
  paperHeight,
  text,
  x,
  y,
  fontSize,
  lineHeight,
  scaleFactor,
  aspectRatio
) {
  const textHeight = getTextHeight(
    text,
    x,
    y,
    paperWidth,
    fontSize,
    lineHeight,
    scaleFactor
  );
  return getCollageDimension(paperWidth, paperHeight - textHeight, aspectRatio);
}

const convertToRGBA = (value) =>
  `rgba(${value.r}, ${value.g}, ${value.b}, ${value.a})`;

function optimizeDimensions(maxPrintWidth, maxPrintHeight, problematicNodes) {
  const ar = maxPrintWidth / maxPrintHeight;
  const optimumTreeSizes = problematicNodes.map((pn) => {
    const newRootWidth =
      (maxPrintWidth * pn.value.naturalWidth) / pn.value.width;
    const newRootHeight = newRootWidth / ar;
    // node with highest score needs smalles tree. smallest tree will satisfy all constraints
    const score = maxPrintWidth - newRootWidth + maxPrintHeight - newRootHeight;
    return [score, [newRootWidth, newRootHeight]];
  });
  optimumTreeSizes.sort((a, b) => b[0] - a[0]);
  return optimumTreeSizes[0][1];
}

export default {
  getCut,
  findClosest,
  findImgPair,
  getAspectRatio,
  handleImageLoad,
  resizeImages,
  wrapText,
  convertToRGBA,
  getCollageDimension,
  downscaleImage,
  convertBase64,
  convertBase64String,
  textCanvasDraw,
  getTextHeight,
  getImageDimensions,
  optimizeDimensions,
  optimizeImage,
};
