import { useState, useEffect } from 'react';
import helpers from 'components/collage/helpers';
import constants from 'static/config/collage';

export default function useImages(initialValue) {
  console.log('use images', initialValue);
  const [newImgCount, setNewImageCount] = useState(0);
  const [newImagesLoaded, setNewImagesLoaded] = useState(0);
  const [imgCount, setImageCount] = useState(0);
  const [loaded, setLoaded] = useState(0);
  const [images, setImages] = useState([]);
  const [imageCountMaxError, setImageCountMaxError] = useState('');
  const [imageCountMinError, setImageCountMinError] = useState('');

  useEffect(() => {
    console.log('setting images');
    if ('images' in initialValue) {
      setImages(initialValue.images);
      if (imgCount === 0) setImageCount(initialValue.images.length);
    }
  }, [initialValue]);

  const getImageData = (imageArr) => {
    const tempImgArr = [...imageArr];
    return tempImgArr.map((img) => {
      const { file, src, ...rest } = img;
      console.log('get image data', img, rest);
      return rest;
    });
  };

  const removeImageData = (imageArr) => {
    const tempImgArr = [...imageArr];
    return tempImgArr.map((img) => {
      const { file, src, ...rest } = img;
      console.log('get image data', img, rest);
      return rest;
    });
  };

  const incrementNewImageCount = () => {
    console.log('incrementing image count');
    setNewImagesLoaded((cnt) => cnt + 1);
  };

  const handleImageData = async (imageDetail) => {
    setImages((arr) => arr.concat([imageDetail]));
    setNewImagesLoaded((cnt) => cnt + 1);
  };

  const handleImageCount = (totalImages) => {
    if (totalImages === 0) {
      setImageCountMinError('');
      return true;
    }
    if (totalImages > 0) {
      if (totalImages < constants.minImages) {
        setImageCountMinError(
          `Image Count Error: Need atleast ${constants.minImages} images to create a collage`
        );
        setImageCountMaxError('');
        return false;
      }
      if (totalImages > constants.maxImages) {
        setImageCountMaxError(
          `Image Count Error: Maximum images allowed in collage is ${constants.maxImages}`
        );
        setImageCountMinError('');
        return false;
      }
      if (
        totalImages >= constants.minImages &&
        totalImages <= constants.maxImages
      ) {
        setImageCountMaxError('');
        setImageCountMinError('');
        return true;
      }
    }
    return false;
  };

  const handleLoad = (event) => {
    const totalImages = event.target.files.length + imgCount;
    const valid = handleImageCount(totalImages);
    if (valid === true) {
      helpers.handleImageLoad(
        event,
        setImageCount,
        setNewImageCount,
        handleImageData
      );
    }
    console.log(
      'total image count',
      event.target.files.length + imgCount,
      imgCount
    );
  };

  const clear = () => {
    setImages([]);
    setImageCount(0);
    setLoaded(0);
    setNewImagesLoaded(0);
  };

  const remove = (title) => {
    const removeIndex = images.findIndex((elem) => elem.title === title);
    const newImagesArr = [
      ...images.slice(0, removeIndex),
      ...images.slice(removeIndex + 1),
    ];
    setImages(newImagesArr);
    setImageCount((cnt) => cnt - 1);
    return newImagesArr;
  };

  return {
    clear,
    handleImageData,
    handleLoad,
    images,
    imgCount,
    loaded,
    newImagesLoaded,
    newImgCount,
    remove,
    setImageCount,
    setImages,
    setLoaded,
    setNewImageCount,
    setNewImagesLoaded,
    getImageData,
    removeImageData,
    incrementNewImageCount,
    imageCountMinError,
    setImageCountMinError,
    imageCountMaxError,
    setImageCountMaxError,
    handleImageCount,
  };
}
