import Box from '@mui/material/Box';
import TextDisplay from 'components/collage/TextDisplay';
import constants from 'static/config/collage';
import Node from './node';
import helpers from './helpers';

export default class BinaryTree {
  constructor(value) {
    if (this.root === undefined) {
      this.root = new Node(value);
      return this;
    }
  }

  addOptions(options) {
    this.root.options = { ...options };
  }

  display() {
    if (this.root !== null) {
      return this.root.display();
    }
    return null;
  }

  replaceImages(newImages) {
    if (this.root !== null) this.root.replaceImages(newImages);
  }

  fromObject(obj) {
    if (obj.root != null) this.root.fromObject(obj.root);
    return this;
  }

  removeImageData() {
    const tempTree = this.copy();
    if (this.root !== null) {
      tempTree.root.removeImageData();
    }
    return tempTree;
  }

  addImageData(imageData) {
    if (this.root !== null) this.root.addImageData(imageData);
  }

  DOMDisplay(
    canvasWidth,
    canvasHeight,
    delImg,
    handleRemoveImage,
    handleCollageImageLoad
  ) {
    const { fontSize, lineHeight, textPadding } = constants;
    const { width, height } = this.root.value;
    const { text, topText, padding, borderRadius, color } = this.root.options;
    const lA = helpers.wrapText(text, 0, 0, canvasWidth, fontSize, lineHeight);
    const textHeight = text.length
      ? lA.length * lineHeight + 2 * textPadding
      : 0;
    let yOffset = (canvasHeight - textHeight - height) / 2;
    const xOffset = (canvasWidth - width) / 2;
    let position = canvasHeight - textHeight;
    if (topText && text.length > 0) {
      yOffset += textHeight;
      position = 0;
    }

    if (this.root !== null) {
      return (
        <Box sx={{ backgroundColor: 'white' }}>
          <Box
            width={canvasWidth}
            height={canvasHeight}
            sx={{
              position: 'relative',
              backgroundColor: helpers.convertToRGBA(color),
            }}
          >
            {this.root.DOMDisplay(
              padding,
              borderRadius,
              delImg,
              handleRemoveImage,
              yOffset,
              xOffset,
              handleCollageImageLoad
            )}
            {text.length ? TextDisplay(lA, position, textHeight) : null}
          </Box>
        </Box>
      );
    }
    return null;
  }

  CanvasDisplay(canvasWidth, canvasHeight, canvasElement, scaleFactor) {
    const { fontSize, lineHeight, textPadding } = constants;
    if (this.root !== null) {
      const { text, topText, padding, borderRadius, color } = this.root.options;
      const { width, height } = this.root.value;
      const lA = helpers.wrapText(
        text,
        0,
        0,
        canvasWidth,
        fontSize * scaleFactor,
        lineHeight * scaleFactor
      );
      const textHeight = text.length
        ? (lA.length * lineHeight + 2 * textPadding) * scaleFactor
        : 0;
      let yOffset = (canvasHeight - textHeight - height) / 2;
      const xOffset = (canvasWidth - width) / 2;
      let position = canvasHeight - textHeight;
      if (topText && text.length > 0) {
        yOffset += textHeight;
        position = 0;
      }
      const ctx = canvasElement.getContext('2d');
      ctx.fillStyle = 'white';
      ctx.fillRect(0, 0, canvasElement.width, canvasElement.height);
      ctx.fillStyle = helpers.convertToRGBA(color);
      ctx.fillRect(0, 0, canvasElement.width, canvasElement.height);
      this.root.CanvasDisplay(
        canvasElement,
        padding * scaleFactor,
        borderRadius * scaleFactor,
        yOffset,
        xOffset
      );
      helpers.textCanvasDraw(
        ctx,
        lA,
        position,
        scaleFactor,
        canvasWidth,
        textHeight
      );
    }
  }

  generateScore() {
    const score = this.root.getScore();
    return Math.min(...score);
  }

  generateCollageTree(images, tar) {
    this.root.generate(images, tar, images.length);
  }

  adjust(th) {
    let trys = 0;
    let bestAdjust = this.copy();
    let bestAspectRatio = this.root.value.aspectRatio;
    const tolerableDifference = this.root.value.targetAspectRatio * (1 - th);
    let ajstCnt = 1000;
    while (trys < 100 && ajstCnt > 0) {
      ajstCnt = this.root.adjust(th);
      trys += 1;
      const diff =
        this.root.value.aspectRatio - this.root.value.targetAspectRatio;
      if (
        Math.abs(diff) <
        Math.abs(bestAspectRatio - this.root.value.targetAspectRatio)
      ) {
        bestAspectRatio = this.root.value.aspectRatio;
        bestAdjust = this.copy();
      }
      if ((diff > 0 && diff < tolerableDifference) || diff === 0) break;
    }

    return [bestAspectRatio, bestAdjust];
  }

  copy() {
    const copyTree = new BinaryTree({});
    copyTree.root = this.root.copy();
    copyTree.root.options = { ...this.root.options };
    return copyTree;
  }

  addSize(canvasWidth, canvasHeight, aspectRatio) {
    const [maxW, maxH] = helpers.getCollageDimension(
      canvasWidth,
      canvasHeight,
      aspectRatio
    );
    this.root.value.width = maxW;
    this.root.value.height = maxH;
    this.root.value.x = 0;
    this.root.value.y = 0;
    const { width, height, x, y, cut } = this.root.value;
    if (this.root.left !== null)
      this.root.left.addSize(width, height, x, y, cut, 'left');
    if (this.root.right !== null)
      this.root.right.addSize(width, height, x, y, cut, 'right');
  }

  validateAddSize() {
    return this.root.validateAddSize();
  }
}
