import PropTypes from 'prop-types';
import Stack from '@mui/material/Stack';
import CollageType from 'components/collage/CollageType';
import CollageImageCount from 'components/collage/CollageImageCount';

export default function CollageMetaInfo({
  collageMetaRef,
  collageType,
  width,
  sizes,
  handleTypeChange,
  imageCount,
}) {
  const handleChange = (event) => {
    const ct = event.target.value;
    handleTypeChange(ct);
  };

  return (
    <Stack
      ref={collageMetaRef}
      direction="row"
      alignItems="center"
      justifyContent="space-around"
      sx={{
        width,
        textAlign: 'middle',
        px: 1,
      }}
    >
      <CollageType
        type={collageType}
        sizes={sizes}
        handleChange={handleChange}
      />
      <CollageImageCount count={imageCount} />
    </Stack>
  );
}

CollageMetaInfo.propTypes = {
  collageType: PropTypes.string.isRequired,
  width: PropTypes.number.isRequired,
  sizes: PropTypes.objectOf(
    PropTypes.shape({
      width: PropTypes.number.isRequired,
      height: PropTypes.number.isRequired,
      text: PropTypes.string.isRequired,
      icon: PropTypes.node,
    })
  ).isRequired,
  handleTypeChange: PropTypes.func.isRequired,
  imageCount: PropTypes.number.isRequired,
  collageMetaRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
  ]).isRequired,
};
