import PropTypes from 'prop-types';
import { useEffect, useState } from 'react';
import { alpha } from '@mui/material/styles';
import Stack from '@mui/material/Stack';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import Typography from '@mui/material/Typography';
import CircularProgress from '@mui/material/CircularProgress';
import PendingIcon from '@mui/icons-material/Pending';
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import CloseIcon from '@mui/icons-material/Close';

function FailedIcon() {
  return (
    <CloseIcon
      color="error"
      size="0.875rem"
      sx={{ border: '2px solid red', borderRadius: '50%' }}
    />
  );
}

function StatusIndicator({ status }) {
  if (status === 'running') {
    return <CircularProgress color="info" size="1rem" />;
  }
  if (status === 'pending') {
    return <PendingIcon color="disabled" size="0.875rem" />;
  }
  if (status === 'done') {
    return <CheckCircleOutlineIcon color="success" size="0.875rem" />;
  }
  if (status === 'failed') {
    return <FailedIcon />;
  }
}

StatusIndicator.propTypes = {
  status: PropTypes.string.isRequired,
};

function Status({ text, status }) {
  const color = {
    pending: alpha('#000', 0.26),
    done: 'black',
    running: 'black',
    failed: 'red',
  };

  return (
    <Stack direction="row" justifyContent="space-between">
      <Typography
        sx={{ fontWeight: 'bold', fontSize: '1rem', color: color[status] }}
      >
        {text}
      </Typography>
      <StatusIndicator status={status} />
    </Stack>
  );
}

Status.propTypes = {
  text: PropTypes.string.isRequired,
  status: PropTypes.string.isRequired,
};

function OverAllStatus({ status, counter }) {
  if (status === 'failed') {
    return (
      <Stack
        direction="row"
        spacing={2}
        justifyContent="center"
        alignItems="center"
        sx={{ width: '100%', p: 1 }}
      >
        <FailedIcon />
        <Typography>Collage creation failed</Typography>
      </Stack>
    );
  }

  if (status === 'running' || status === 'pending') return null;

  if (status === 'success') {
    return (
      <Stack
        direction="row"
        spacing={2}
        justifyContent="center"
        alignItems="center"
        sx={{ width: '100%', p: 1 }}
      >
        <CheckCircleOutlineIcon color="success" sx={{ fontSize: '2rem' }} />
        <Typography>
          Collage creation successful. Navigation to collages in {counter}{' '}
          seconds...
        </Typography>
      </Stack>
    );
  }

  return null;
}

OverAllStatus.propTypes = {
  status: PropTypes.string.isRequired,
  counter: PropTypes.number.isRequired,
};

export default function SaveStatusDialog({ open, status }) {
  let overAllStatus = 'pending';
  const [counter, setCounter] = useState(-1);
  useEffect(() => {
    let timerId;
    if (counter > 0 && overAllStatus === 'success') {
      timerId = setTimeout(() => setCounter(counter - 1), 1000);
    }
    return () => clearTimeout(timerId);
  }, [counter, overAllStatus]);

  const statusArr = Object.values(status).map((val) => val.status);
  if (statusArr.includes('failed')) {
    overAllStatus = 'failed';
  } else if (statusArr.includes('running')) {
    overAllStatus = 'running';
  } else if (!statusArr.includes('pending')) {
    console.log('setting counter', counter, status);
    if (counter === -1) {
      setCounter(3);
    }
    overAllStatus = 'success';
  }

  return (
    <Dialog open={open} fullWidth>
      <DialogTitle sx={{ textAlign: 'center' }}>Save Collage</DialogTitle>
      <DialogContent dividers>
        <Stack direction="column" justifyContent="center" spacing={2}>
          {Object.values(status).map((st) => (
            <Status key={st.text} text={st.text} status={st.status} />
          ))}
        </Stack>
      </DialogContent>
      <OverAllStatus status={overAllStatus} counter={counter} />
    </Dialog>
  );
}

SaveStatusDialog.propTypes = {
  open: PropTypes.bool.isRequired,
  status: PropTypes.objectOf(
    PropTypes.shape({
      text: PropTypes.string,
      status: PropTypes.string,
    })
  ).isRequired,
};
