import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';
import { alpha } from '@mui/material/styles';
import constants from 'static/config/collage';

export default function TextDisplay(textArray, position, textHeight) {
  const { fontSize, lineHeight, textPadding } = constants;
  console.log('text display', textArray, position, textPadding);
  return (
    <>
      <Stack
        sx={{
          height: textHeight,
          backgroundColor: alpha('#1c1c1c', 0.7),
          position: 'absolute',
          top: position,
          width: '100%',
        }}
      />
      {
        // eslint-disable-next-line react/destructuring-assignment
        textArray.map((chunk) => (
          <Typography
            key={chunk[2]}
            sx={{
              width: '100%',
              position: 'absolute',
              top: position + chunk[2] + textPadding,
              paddingX: 1,
              color: 'button.main',
              textAlign: 'center',
              fontFamily: 'Arial',
              fontSize: `${fontSize}px`,
              lineHeight: `${lineHeight}px`,
              letterSpacing: 'normal',
            }}
          >
            {chunk[0]}
          </Typography>
        ))
      }
    </>
  );
}
