import PropTypes from 'prop-types';
import { useEffect } from 'react';
import { alpha } from '@mui/material/styles';
import * as yup from 'yup';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import constants from 'static/config/collage';
import utils from 'utils';

export default function TextControl({
  text,
  handleTextChange,
  topText,
  handlePositionChange,
  handleTextSubmit,
  textError,
  handleTextError,
}) {
  const textSchema = yup
    .object({
      text: yup
        .string()
        .max(
          constants.maxColllageTextLength,
          `Max allowed length is ${constants.maxColllageTextLength}`
        ),
    })
    .required();

  const isValid = async () => {
    const textValid = await utils.isFieldValid(
      'text',
      text,
      handleTextError,
      textSchema
    );
    return textValid;
  };

  const handleKeyDown = (e) => {
    if (e.keyCode === 13 && e.shiftKey === false) {
      handleTextSubmit(e);
    }
  };

  useEffect(() => isValid(), [text]);

  return (
    <Stack
      sx={{
        backgroundColor: 'transparent',
      }}
    >
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        spacing={2}
      >
        <Typography
          sx={{ fontWeight: 'bold', color: 'gray', fontSize: '0.87rem' }}
        >
          Collage Text
        </Typography>
        <FormGroup row>
          <FormControlLabel
            disableTypography
            control={
              <Checkbox
                size="small"
                checked={topText === false}
                onChange={() => handlePositionChange(false)}
                sx={{ color: 'white', '&.Mui-checked': { color: 'white' } }}
              />
            }
            label={
              <Typography fontSize="0.8rem" fontWeight="bold">
                Bottom
              </Typography>
            }
            sx={{ color: 'gray', fontWeight: 'bold' }}
          />
          <FormControlLabel
            disableTypography
            control={
              <Checkbox
                size="small"
                checked={topText === true}
                onChange={() => handlePositionChange(true)}
                sx={{ color: 'gray', '&.Mui-checked': { color: 'white' } }}
              />
            }
            label={
              <Typography fontSize="0.8rem" fontWeight="bold">
                Top
              </Typography>
            }
            sx={{ color: 'gray', fontWeight: 'bold', fontSize: '0.8rem' }}
          />
        </FormGroup>
      </Stack>
      <TextField
        variant="outlined"
        label=""
        defaultValue={text}
        onKeyDown={handleKeyDown}
        size="small"
        sx={{ width: '100%', mb: 1 }}
        onChange={handleTextChange}
        InputProps={{ sx: { backgroundColor: alpha('#fff', 0.9) } }}
        helperText={
          <Typography sx={{ fontSize: '0.75rem' }}>{textError}</Typography>
        }
        error={!!textError}
      />
    </Stack>
  );
}

TextControl.propTypes = {
  text: PropTypes.string.isRequired,
  handleTextChange: PropTypes.func.isRequired,
  topText: PropTypes.bool.isRequired,
  handlePositionChange: PropTypes.func.isRequired,
  handleTextSubmit: PropTypes.func.isRequired,
  textError: PropTypes.string.isRequired,
  handleTextError: PropTypes.func.isRequired,
};
