import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';

import * as yup from 'yup';
// import { alpha } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';
// import { alpha } from '@mui/material/styles';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import { Typography, Box, Link } from '@mui/material';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import ProfileLink from 'components/profile';
import { Branding, BrandLogo } from 'components/brand';
import constants from 'static/config/collage';
import utils from 'utils';

function HeaderNameSection({ name, handleChange, nameError, handleNameError }) {
  const [editable, setEditable] = useState(false);
  const handleClick = () => setEditable(true);
  const nameSchema = yup
    .object({
      name: yup
        .string()
        .max(
          constants.maxNameLength,
          `Max allowed length is ${constants.maxNameLength}`
        ),
    })
    .required();

  const isValid = async () => {
    const nameValid = await utils.isFieldValid(
      'name',
      name,
      handleNameError,
      nameSchema
    );
    return nameValid;
  };

  const handleNameChange = (e) => handleChange(e.target.value);

  const handleBlur = async (e) => {
    if (e.target.value === '') {
      handleChange('Untitled');
    } else {
      handleChange(e.target.value);
      const valid = await isValid();
      if (valid === false) {
        return;
      }
    }
    setEditable(false);
  };

  const handleKeyDown = (e) => {
    if (e.keyCode === 13 && e.shiftKey === false) {
      handleBlur(e);
    }
  };

  useEffect(() => isValid(), [name]);

  return editable === true ? (
    <TextField
      color="primary"
      variant="filled"
      label="Set Collage Name"
      defaultValue={name}
      autoFocus
      size="small"
      onChange={handleNameChange}
      onBlur={handleBlur}
      onKeyDown={handleKeyDown}
      InputLabelProps={{
        color: 'secondary',
        focused: true,
      }}
      InputProps={{
        sx: {
          color: 'white',
        },
      }}
      helperText={
        <Typography
          variant="body"
          sx={{
            fontWeight: 'bold',
            color: 'secondary.main',
          }}
        >
          {nameError}
        </Typography>
      }
      error={!!nameError}
      sx={{
        position: 'absolute',
        left: '50%',
        transform: 'translate(-50%, 0)',
        cursor: 'text',
      }}
    />
  ) : (
    <Typography
      variant="h5"
      onClick={handleClick}
      sx={{
        color: 'gray',
        position: 'absolute',
        left: '50%',
        transform: 'translate(-50%, 0)',
      }}
    >
      {name}
    </Typography>
  );
}

HeaderNameSection.propTypes = {
  name: PropTypes.string.isRequired,
  handleChange: PropTypes.func.isRequired,
  nameError: PropTypes.string.isRequired,
  handleNameError: PropTypes.func.isRequired,
};

export default function Header({
  headerRef,
  name,
  handleNameChange,
  nameError,
  handleNameError,
}) {
  const belowmd = useMediaQuery((theme) => theme.breakpoints.down('md'));
  return (
    <Paper
      ref={headerRef}
      sx={{
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        p: 1,
        backgroundColor: 'primary.main',
        borderRadius: 0,
      }}
      elevation={0}
    >
      <Box sx={{ postion: 'absolute', left: '5px' }}>
        <Stack direction="row" alignItems="center" spacing={2}>
          <Link href="/collages" color="white" underline="none">
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <ArrowBackIcon fontSize="medium" />
            </Box>
          </Link>
          {belowmd ? (
            <BrandLogo width="50px" height="50px" />
          ) : (
            <Branding width="50px" height="50px" />
          )}
        </Stack>
      </Box>
      <HeaderNameSection
        name={name}
        handleChange={handleNameChange}
        nameError={nameError}
        handleNameError={handleNameError}
      />
      <ProfileLink />
    </Paper>
  );
}

Header.propTypes = {
  name: PropTypes.string.isRequired,
  headerRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
  ]).isRequired,
  handleNameChange: PropTypes.func.isRequired,
  nameError: PropTypes.string.isRequired,
  handleNameError: PropTypes.func.isRequired,
};
