import { useState, useEffect } from 'react';
import treeUtils from 'components/collage/treeUtils';
import BinaryTree from 'components/collage/binaryTree';

export default function useTree(initialValue, loadingCallback = () => {}) {
  const [tree, setTree] = useState(new BinaryTree({}));

  useEffect(() => {
    console.log('use Tree useEffect');
    const newTree = new BinaryTree({});
    if (
      'details' in initialValue &&
      Object.keys(initialValue.details).length > 0
    ) {
      console.log('initial value in tree', initialValue);
      newTree.fromObject(initialValue.details.tree);
      if ('options' in initialValue.details.tree.root) {
        loadingCallback((opts) => ({
          ...opts,
          ...initialValue.details.tree.root.options,
        }));
      }
      console.log('setting tree');
      setTree(newTree);
    }
  }, [initialValue]);

  const clear = () => setTree({});

  const reAdjust = (imagesArr, tolerance, imageWidth, imageHeight, options) => {
    const [, bestAspectRatioTree] = treeUtils.findBestAdjustTree(
      imagesArr,
      tolerance,
      imageWidth,
      imageHeight
    );
    bestAspectRatioTree.addOptions(options);
    setTree(bestAspectRatioTree);
  };

  console.log('use Tree', tree);
  return { tree, setTree, clear, reAdjust };
}
