import PropTypes from 'prop-types';
import constants from 'static/config/collage';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import EmptyCollage from 'components/collage/EmptyCollage';
import Loading from 'components/Loading';
import AddPhotos from 'components/AddPhotos';

export default function View({
  open,
  saveOpen,
  saveStatusOpen,
  status,
  dimensionProps,
  imageProps,
  header,
  actions,
  downloadDialog,
  saveDialog,
  saveStatusDialog,
  collage,
  controls,
  collageMetaInfo,
}) {
  const { width, height, maxWidth, maxHeight, collageMetaRef } = dimensionProps;
  console.log(maxHeight, maxWidth);
  const metaHeight = collageMetaRef.current
    ? collageMetaRef.current.offsetHeight
    : 0;
  let showControls = false;
  const { images, imgCount, handleLoad } = imageProps;
  if (images.length > 0) {
    showControls = true;
  }
  let content;
  if (status === 'fetching') {
    content = (
      <EmptyCollage
        width={width}
        height={height}
        loadComponent={<Loading title="Fetching collage..." />}
      />
    );
  }

  if (status === 'preparing') {
    content = (
      <>
        {collage}
        <Loading title="Preparing collage..." />
      </>
    );
  }

  if (status === 'loading') {
    content = (
      <EmptyCollage
        width={width}
        height={height}
        loadComponent={
          <>
            <Loading
              title={`Loading (${imageProps.newImagesLoaded} / ${imgCount}) images...`}
            />
            {collage}
          </>
        }
      />
    );
  }

  if (status === 'empty') {
    content = (
      <EmptyCollage
        width={width}
        height={height}
        loadComponent={
          width ? (
            <Stack direction="column" spacing={2}>
              <AddPhotos handleChange={handleLoad} />
              <Typography
                sx={{
                  backgroundColor: 'faded.black',
                  p: 1,
                  color: 'form.stepper',
                  fontWeight: 'bold',
                  fontSize: '0.7rem',
                }}
              >
                Tip: Select {constants.minImages} - {constants.maxImages} images
              </Typography>
            </Stack>
          ) : (
            <div />
          )
        }
      />
    );
  }

  if (status === 'ready') {
    content = collage;
  }
  const leftPos =
    (Math.min(window.innerWidth, window.screen.availWidth) - maxWidth) / 2;
  console.log(
    'window.innerWidth',
    window.innerWidth,
    'maxWidth',
    maxWidth,
    'leftPos',
    leftPos
  );

  return (
    <Box
      component="div"
      sx={{
        position: 'relative',
      }}
    >
      {header}
      {showControls ? actions : null}
      {open ? downloadDialog : null}
      {saveOpen ? saveDialog : null}
      {saveStatusOpen ? saveStatusDialog : null}
      <Stack
        direction="column"
        width={maxWidth}
        height={maxHeight + metaHeight}
        sx={{
          position: 'absolute',
          left: leftPos,
          alignItems: 'center',
          justifyContent: 'center',
          backgroundColor: 'gray',
        }}
      >
        {collageMetaInfo}
        {content}
      </Stack>
      {showControls ? controls : null}
    </Box>
  );
}

View.propTypes = {
  dimensionProps: PropTypes.shape({
    maxWidth: PropTypes.number.isRequired,
    maxHeight: PropTypes.number.isRequired,
    width: PropTypes.number.isRequired,
    height: PropTypes.number.isRequired,
    imageHeight: PropTypes.number.isRequired,
    collageMetaRef: PropTypes.oneOfType([
      PropTypes.func,
      PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
    ]).isRequired,

    headerRef: PropTypes.oneOfType([
      PropTypes.func,
      PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
    ]).isRequired,
    actionsRef: PropTypes.oneOfType([
      PropTypes.func,
      PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
    ]).isRequired,
    footerRef: PropTypes.oneOfType([
      PropTypes.func,
      PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
    ]).isRequired,
  }).isRequired,
  imageProps: PropTypes.shape({
    images: PropTypes.arrayOf(
      PropTypes.shape({
        title: PropTypes.string.isRequired,
        src: PropTypes.string.isRequired,
        naturalHeight: PropTypes.number.isRequired,
        naturalWidth: PropTypes.number.isRequired,
        aspectRatio: PropTypes.number.isRequired,
        type: PropTypes.string.isRequired,
      })
    ).isRequired,
    imgCount: PropTypes.number.isRequired,
    handleLoad: PropTypes.func.isRequired,
    loaded: PropTypes.number.isRequired,
    newImagesLoaded: PropTypes.number.isRequired,
  }).isRequired,
  open: PropTypes.bool.isRequired,
  saveOpen: PropTypes.bool.isRequired,
  saveStatusOpen: PropTypes.bool.isRequired,
  status: PropTypes.string.isRequired,
  header: PropTypes.node.isRequired,
  actions: PropTypes.node.isRequired,
  collage: PropTypes.node.isRequired,
  controls: PropTypes.node.isRequired,
  downloadDialog: PropTypes.node.isRequired,
  saveDialog: PropTypes.node.isRequired,
  saveStatusDialog: PropTypes.node.isRequired,
  collageMetaInfo: PropTypes.node.isRequired,
};
