import PropTypes from 'prop-types';
import { useState, useEffect } from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import Typography from '@mui/material/Typography';

export default function HandleError({ errors, children, handleClose }) {
  const [open, setOpen] = useState(false);
  useEffect(() => setOpen(true), []);
  return errors.length > 0 ? (
    <Dialog open={open} fullWidth>
      <DialogTitle
        sx={{ textAlign: 'center', color: 'red', fontWeight: 'bold' }}
      >
        Error(s)
      </DialogTitle>
      <DialogContent
        dividers
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
        }}
      >
        <DialogContentText sx={{ display: 'inline', pr: 1 }}>
          <Typography sx={{ fontWeight: 'bold' }}>
            Please fix the following errors
          </Typography>
          <List sx={{ px: 2 }}>
            {errors.map((err) => (
              <ListItem sx={{ display: 'list-item', listStyleType: 'disc' }}>
                {err}
              </ListItem>
            ))}
          </List>
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button
          variant="outlined"
          onClick={() => {
            handleClose();
            setOpen(false);
          }}
        >
          Close
        </Button>
      </DialogActions>
    </Dialog>
  ) : (
    children
  );
}

HandleError.propTypes = {
  errors: PropTypes.arrayOf(PropTypes.string).isRequired,
  children: PropTypes.node.isRequired,
  handleClose: PropTypes.func.isRequired,
};
