import PropTypes from 'prop-types';
import { useState, useRef, useEffect } from 'react';
import Stack from '@mui/material/Stack';
import RefreshIcon from '@mui/icons-material/Refresh';
import EditIcon from '@mui/icons-material/Edit';
import PhotoIcon from '@mui/icons-material/Photo';
import AutoAwesomeMosaicIcon from '@mui/icons-material/AutoAwesomeMosaic';
import DesktopDrawer from 'components/collage/DesktopDrawer';
import DBNA from 'components/collage/DesktopActionItem';
import ControlsDivider from 'components/collage/ControlsDivider';
import List from '@mui/material/List';
import DeleteIcon from '@mui/icons-material/Delete';
// import { alpha } from '@mui/material/styles';

import ClearCollageDialog from 'components/collage/ClearCollageDialog';

export default function DesktopControls({
  innerRef,
  onRefresh,
  photosComp,
  editComp,
  handleClear,
  clearDelete,
  saveType,
}) {
  const [imageOptionsOpen, setImageOptionsOpen] = useState(false);
  const [editOptionsOpen, setEditOptionsOpen] = useState(false);
  const [clearOpen, setClearOpen] = useState(false);
  const editRef = useRef(null);

  const closeAll = () => {
    clearDelete();
    setImageOptionsOpen(false);
    setEditOptionsOpen(false);
    setClearOpen(false);
  };

  useEffect(() => {
    function handleClickOutside(event) {
      if (editRef.current && !editRef.current.contains(event.target)) {
        closeAll();
      }
    }
    // Bind the event listener
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      // Unbind the event listener on clean up
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [editRef]);

  const handlePhotosClick = () => {
    const ioo = imageOptionsOpen;
    setImageOptionsOpen((io) => !io);
    if (ioo === true) closeAll();
    // closeAll();
    // if (imageOptionsOpen) {
    //   setImageOptionsOpen((io) => !io);
    //   closeAll();
    // } else {
    //   closeAll();
    //   setImageOptionsOpen(true);
    // }
  };

  const handleEditClick = () => {
    if (editOptionsOpen) {
      setEditOptionsOpen(false);
    } else {
      closeAll();
      setEditOptionsOpen(true);
    }
  };
  const handleRearrange = () => {
    closeAll();
    onRefresh();
  };

  const handleClearClick = () => {
    console.log('clear open', clearOpen);
    if (clearOpen) {
      setClearOpen(false);
    } else {
      closeAll();
      setClearOpen(true);
    }
  };

  const handleClearClose = () => setClearOpen(false);
  const handleClearSubmit = () => {
    handleClearClose();
    handleClear();
  };

  console.log('clear open', clearOpen);
  console.log('rendering desktop controls');
  return (
    <DesktopDrawer anchor="left">
      <Stack direction="row" spacing={2} alignItems="center" ref={innerRef}>
        <List
          sx={{
            backgroundColor: 'black',
            borderRadius: '0 10px 10px 0',
          }}
        >
          <DBNA
            label="Rearrange"
            icon={<AutoAwesomeMosaicIcon />}
            handleChange={handleRearrange}
          />
          <ControlsDivider />
          <DBNA
            label="Photos"
            icon={<PhotoIcon />}
            handleChange={handlePhotosClick}
          />
          <ControlsDivider />
          <DBNA
            label="Edit"
            icon={<EditIcon />}
            handleChange={handleEditClick}
          />
          <ControlsDivider />
          <DBNA
            label={saveType === 'new' ? 'Clear' : 'Delete'}
            icon={saveType === 'new' ? <RefreshIcon /> : <DeleteIcon />}
            handleChange={handleClearClick}
          />
        </List>
        {imageOptionsOpen ? photosComp : null}
        {editOptionsOpen ? <div ref={editRef}>{editComp}</div> : null}
        <ClearCollageDialog
          open={clearOpen}
          onSubmit={handleClearSubmit}
          onCancel={handleClearClose}
          saveType={saveType}
        />
      </Stack>
    </DesktopDrawer>
  );
}

DesktopControls.propTypes = {
  innerRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
  ]).isRequired,
  onRefresh: PropTypes.func.isRequired,
  photosComp: PropTypes.node.isRequired,
  editComp: PropTypes.node.isRequired,
  handleClear: PropTypes.func.isRequired,
  clearDelete: PropTypes.func.isRequired,
  saveType: PropTypes.oneOf(['new', 'existing']).isRequired,
};
