import { useEffect, useState /* , useLayoutEffect */ } from 'react';
import useDimensions from 'hooks/collage/dimensions';
import useOptions from 'hooks/collage/options';
import useActions from 'hooks/collage/actions';
import useImages from 'hooks/collage/images';
import useTree from 'hooks/collage/tree';

import helpers from 'components/collage/helpers';
import apiHelpers from 'components/collage/apiHelpers';
import collageapi from 'api/collage';
import constants from 'static/config/collage';

export default function useBase(initialValue) {
  const {
    defaultCollageName,
    defaultCollageType,
    paperSizes,
    tolerance,
    thumbnailHeight,
    fontSize,
    lineHeight,
    textPadding,
  } = constants;

  const [details, setDetails] = useState(initialValue);
  const [collageName, setCollageName] = useState(defaultCollageName);
  const [collageType, setCollageType] = useState(defaultCollageType);
  const [uploadedImages, setUploadedImages] = useState(0);
  const dimensions = useDimensions();
  const options = useOptions();
  const actions = useActions();
  const images = useImages(details);
  const treeState = useTree(details, options.setOptions);
  const [thumbnail, setThumbnail] = useState({});

  const initialStatus = () => ({
    details: {
      text: 'Saving Details',
      status: 'pending',
    },
    images: {
      text: `Saving Images (${uploadedImages} / ${images.images.length})`,
      status: 'pending',
    },
    finalizing: {
      text: 'Finalizing Collage',
      status: 'pending',
    },
  });

  const [saveStatus, setSaveStatus] = useState(initialStatus);

  const collageID =
    'details' in initialValue && 'collage_id' in initialValue.details
      ? initialValue.details.collage_id
      : Date.now();

  const collageCallback = () => {};
  const handleCollageTypeChange = (ct) => setCollageType(ct);
  const getAspectRatio = () =>
    paperSizes[collageType].width / paperSizes[collageType].height;

  const handleNameChange = (newName) => setCollageName(newName);
  const uploadCallback = () => setUploadedImages((cnt) => cnt + 1);

  const updateStatus = (key, status) => {
    const value = { ...saveStatus[key], status };
    setSaveStatus((st) => ({ ...st, [key]: value }));
  };

  const updateImagesStatus = () => {
    const imagesText = `Saving Images (${uploadedImages} / ${images.imgCount})`;
    let value = { ...saveStatus.images, text: imagesText };
    if (uploadedImages === images.imgCount && images.imgCount > 0) {
      value = { ...value, status: 'done' };
    }
    setSaveStatus((st) => ({ ...st, images: value }));
  };

  const handleClear = () => {
    treeState.clear();
    images.clear();
    options.clear();
  };

  const handleRemoveImage = (imgTitle) => {
    const newImagesArr = images.remove(imgTitle);
    treeState.reAdjust(
      newImagesArr,
      tolerance,
      dimensions.imageWidth,
      dimensions.imageHeight,
      options.options
    );
  };

  const generateThumbnail = () => {
    const ar = treeState.tree.root.value.aspectRatio;
    const thumbnailWidth = thumbnailHeight * ar;
    const thumbnailCanvas = document.createElement('canvas');
    const thumbnailTree = treeState.tree.copy();
    thumbnailTree.root.options.text = '';
    thumbnailTree.addSize(thumbnailWidth, thumbnailHeight, ar);
    thumbnailCanvas.width = thumbnailWidth;
    thumbnailCanvas.height = thumbnailHeight;
    thumbnailTree.CanvasDisplay(
      thumbnailWidth,
      thumbnailHeight,
      thumbnailCanvas,
      thumbnailWidth / dimensions.width
    );

    // create and update details
    const thumbnailDetails = {
      title: `thumbnail-${collageID}.png`,
      src: '',
      naturalHeight: thumbnailHeight,
      naturalWidth: thumbnailWidth,
      aspectRatio: ar,
      type: 'image/png',
    };
    setThumbnail(thumbnailDetails);
    return [thumbnailDetails, thumbnailCanvas];
  };

  const getDetails = (name, thumbnailDetails) => {
    setCollageName(name);
    return {
      collage_id: collageID.toString(),
      name,
      collage_type: collageType,
      created_at: '2021-02-18T21:54:42.123Z',
      last_updated_at: '2021-02-18T21:54:45.123Z',
      images: images.getImageData(images.images),
      tree: treeState.tree.removeImageData(),
      thumbnail: thumbnailDetails,
    };
  };

  const updateCollage = async (name, getURLS) => {
    const [thumbDets, thumbnailCanvas] = generateThumbnail();

    // Upload collage details
    updateStatus('details', 'running');
    const getURLSResp = await getURLS(collageID, getDetails(name, thumbDets));
    if ('status' in getURLSResp && getURLSResp.status === 'error') {
      updateStatus('details', 'failed');
      return false;
    }

    updateStatus('details', 'done');

    // Image Upload start
    updateStatus('images', 'running');
    const uploadPromises = await Promise.allSettled(
      apiHelpers.uploadCollageImages(
        thumbnailCanvas,
        getURLSResp,
        uploadCallback,
        images.images,
        collageID
      )
    );
    updateStatus('images', 'done');

    // Verify upload promises
    updateStatus('finalizing', 'running');
    const valid = apiHelpers.verifyPromises(uploadPromises);
    updateStatus('finalizing', valid === true ? 'done' : 'failed');
    return valid;
  };

  const newCollage = async (name) => updateCollage(name, collageapi.newCollage);
  const saveCollage = async (name) =>
    updateCollage(name, collageapi.saveCollage);

  const updateTreeAspectRatio = () => {
    const ar = getAspectRatio();
    if (
      Object.keys(treeState.tree).length > 0 &&
      'aspectRatio' in treeState.tree.root.value &&
      treeState.tree.root.value.aspectRatio !== ar
    ) {
      const tempTree = treeState.tree.copy();
      tempTree.value = { ...tempTree.value, aspectRatio: ar };
      tempTree.root.value = { ...tempTree.root.value, targetAspectRatio: ar };
      treeState.setTree(tempTree);
      if (images.images.length > 1) actions.setRefresh(true);
    }
  };

  const handleInitialValue = () => {
    setDetails(initialValue);
    if ('details' in initialValue) {
      const iDets = initialValue.details;
      if ('name' in iDets && collageName === defaultCollageName) {
        setCollageName(iDets.name);
      }
      if ('collage_type' in iDets && collageType === defaultCollageType) {
        setCollageType(initialValue.details.collage_type);
      }
    }
  };

  const rearrangeCollage = () => {
    if (
      (images.newImgCount > 0 &&
        images.newImgCount === images.newImagesLoaded) ||
      actions.refresh
    ) {
      images.setNewImageCount(0);
      images.setNewImagesLoaded(0);
      treeState.reAdjust(
        images.images,
        tolerance,
        dimensions.width,
        dimensions.height - dimensions.textHeight,
        options.options
      );
      actions.setRefresh(false);
    }
  };

  const setCollageDimensions = () => {
    const ar = getAspectRatio();
    const { maxWidth, maxHeight } = dimensions;
    const [w, h] = helpers.getCollageDimension(maxWidth, maxHeight, ar);
    dimensions.setWidth(w);
    dimensions.setHeight(h);
  };

  const updateCollageWithTextDimensions = () => {
    const { width, height, setTextHeight, setImageWidth, setImageHeight } =
      dimensions;
    const { text } = options.options;
    if (Object.keys(treeState.tree).length > 0) {
      const tH = helpers.wrapText(text, 0, 0, width, fontSize, lineHeight);
      const totalTextHeight =
        text.length > 0 ? tH.length * lineHeight + 2 * textPadding : 0;
      const [w, h] = helpers.getCollageDimension(
        width,
        height - totalTextHeight,
        treeState.tree.root.value.aspectRatio
      );
      setTextHeight(totalTextHeight);
      setImageWidth(w);
      setImageHeight(h);
    }
  };

  useEffect(updateImagesStatus, [uploadedImages, images.imgCount]);
  useEffect(handleInitialValue, [initialValue]);

  useEffect(updateCollageWithTextDimensions, [
    dimensions.width,
    dimensions.height,
    treeState.tree,
    options.options.text,
  ]);

  useEffect(updateTreeAspectRatio, [collageType]);
  useEffect(setCollageDimensions, [
    dimensions.maxWidth,
    dimensions.maxHeight,
    collageType,
  ]);

  useEffect(rearrangeCollage, [
    images.newImgCount,
    images.newImagesLoaded,
    actions.refresh,
  ]);

  return [
    collageType,
    getDetails,
    collageName,
    thumbnail,
    collageID,
    dimensions,
    options,
    actions,
    images,
    treeState,
    handleClear,
    collageCallback,
    handleRemoveImage,
    newCollage,
    saveStatus,
    saveCollage,
    handleCollageTypeChange,
    handleNameChange,
  ];
}
