import PropTypes from 'prop-types';
import { useState, useEffect, useRef } from 'react';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import { alpha } from '@mui/material/styles';

import MainControls from 'components/collage/MainControls';
import EditOptions from 'components/collage/EditOptions';
import ImageOptions from 'components/collage/ImageOptions';
import ClearCollageDialog from 'components/collage/ClearCollageDialog';

export default function MobileControls({
  optionProps,
  actionProps,
  dimensionProps,
  handleClear,
  saveType,
  paddingControl,
  radiusControl,
  colorControl,
  textControl,
  editControl,
  handleEditControl,
  editOptionsValue,
  handleEditOptionsValue,
}) {
  const { options, handleColorClick, displayColorPicker } = optionProps;
  const { onRefresh, onDelete, clearDelete } = actionProps;
  const { footerRef, handleImageAddClick } = dimensionProps;

  const [mainOpen, setMainOpen] = useState(true);
  const [value, setValue] = useState('');
  const [editOpen, setEditOpen] = useState(false);
  const [clearOpen, setClearOpen] = useState(false);
  const [imageOpen, setImageOpen] = useState(false);
  const [imageValue, setImageValue] = useState('');
  const editRef = useRef(null);

  useEffect(() => {
    function handleClickOutside(event) {
      if (editRef.current && !editRef.current.contains(event.target)) {
        handleEditControl(null);
      }
    }
    // Bind the event listener
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      // Unbind the event listener on clean up
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [editRef]);

  const handleImageOptionsClose = () => {
    setImageOpen(false);
    setImageValue('');
    setMainOpen(true);
    clearDelete();
    setValue('');
  };

  const handleEditOptionsClose = () => {
    setEditOpen(false);
    handleEditControl(null);
    handleEditOptionsValue('');
    setMainOpen(true);
    setValue('');
  };

  const handleEditOptionsClick = (e, nV) => {
    console.log('handle edit options click');
    handleEditOptionsValue(nV);
    if (nV === 'padding') {
      handleEditControl(paddingControl);
    } else if (nV === 'border radius') {
      handleEditControl(radiusControl);
    } else if (nV === 'color') {
      handleColorClick();
    } else if (nV === 'text') {
      console.log(nV);
      handleEditControl(textControl);
    }
  };

  useEffect(() => {
    if (editOpen && editOptionsValue === 'padding') {
      handleEditControl(paddingControl);
    }
  }, [options.padding, editOpen]);

  useEffect(() => {
    if (editOpen && editOptionsValue === 'border radius') {
      handleEditControl(radiusControl);
    }
  }, [options.borderRadius, editOpen]);

  useEffect(() => {
    if (editOpen && editOptionsValue === 'color' && !!displayColorPicker) {
      handleEditControl(colorControl);
    }
  }, [options.color, editOpen, displayColorPicker]);

  useEffect(() => {
    console.log('text control useEffect');
    if (editOpen && editOptionsValue === 'text') {
      handleEditControl(textControl);
    }
  }, [options.text, editOpen, options.topText]);

  const handleImageClose = () => {
    setImageValue('');
    setImageOpen(false);
    setMainOpen(true);
  };

  const handleChange = (event, newValue) => {
    clearDelete();
    handleImageClose();
    handleEditOptionsClose();
    setValue(newValue);
    if (newValue === 'rearrange') {
      onRefresh();
    } else if (newValue === 'photo') {
      setMainOpen(false);
      setImageOpen(true);
    } else if (newValue === 'edit') {
      setMainOpen(false);
      setEditOpen(true);
    } else if (newValue === 'clear') {
      setClearOpen(true);
    } else {
      console.log('functionality for ', newValue, 'not yet implemented');
    }
  };

  const handleImageAdd = () => {
    clearDelete();
    handleImageAddClick();
  };

  const handleClearSubmit = () => {
    handleClear();
    setClearOpen(false);
    setValue('');
  };

  const handleClearCancel = () => {
    setClearOpen(false);
    setValue('');
  };

  console.log('rerendering', editControl);
  return (
    <Paper
      ref={footerRef}
      sx={{
        position: 'fixed',
        top: Math.min(window.innerHeight, window.screen.availHeight),
        // bottom: 0,
        // left: 0,
        // right: 0,
        // mt: 1,
        backgroundColor: 'black',
        borderRadius: 0,
        width: Math.min(window.innerWidth, window.screen.availWidth),
        transform: 'translate(0, -100%)',
      }}
      elevation={4}
    >
      <ClearCollageDialog
        open={clearOpen}
        onSubmit={handleClearSubmit}
        onCancel={handleClearCancel}
        saveType={saveType}
      />
      {editControl !== null ? (
        <Box
          ref={editRef}
          sx={{
            backgroundColor: alpha('#fff', 0.15),
            p: 1,
          }}
        >
          {editControl}
        </Box>
      ) : null}
      <ImageOptions
        open={imageOpen}
        value={imageValue}
        handleDelete={onDelete}
        handleAdd={handleImageAdd}
        handleOptionsClose={handleImageOptionsClose}
      />
      <EditOptions
        color={options.color}
        editOptionsValue={editOptionsValue}
        open={editOpen}
        handleOptionsClose={handleEditOptionsClose}
        handleEditOptionsClick={handleEditOptionsClick}
      />
      {mainOpen ? (
        <MainControls
          value={value}
          handleChange={handleChange}
          saveType={saveType}
        />
      ) : null}
    </Paper>
  );
}

MobileControls.propTypes = {
  optionProps: PropTypes.shape({
    options: PropTypes.shape({
      padding: PropTypes.number.isRequired,
      borderRadius: PropTypes.number.isRequired,
      text: PropTypes.string.isRequired,
      topText: PropTypes.bool.isRequired,
      color: PropTypes.shape({
        r: PropTypes.number.isRequired,
        g: PropTypes.number.isRequired,
        b: PropTypes.number.isRequired,
        a: PropTypes.number.isRequired,
      }),
    }).isRequired,
    handlePaddingChange: PropTypes.func.isRequired,
    handleRadiusChange: PropTypes.func.isRequired,
    handleColorChange: PropTypes.func.isRequired,
    handleColorClose: PropTypes.func.isRequired,
    handleColorClick: PropTypes.func.isRequired,
    displayColorPicker: PropTypes.bool.isRequired,
    handleTextChange: PropTypes.func.isRequired,
    handlePositionChange: PropTypes.func.isRequired,
  }).isRequired,
  actionProps: PropTypes.shape({
    onRefresh: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
    clearDelete: PropTypes.func.isRequired,
  }).isRequired,
  handleClear: PropTypes.func.isRequired,
  dimensionProps: PropTypes.shape({
    handleImageAddClick: PropTypes.func.isRequired,
    footerRef: PropTypes.oneOfType([
      PropTypes.func,
      PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
    ]).isRequired,
  }).isRequired,
  saveType: PropTypes.oneOf(['new', 'existing']).isRequired,
  paddingControl: PropTypes.node.isRequired,
  radiusControl: PropTypes.node.isRequired,
  colorControl: PropTypes.node.isRequired,
  textControl: PropTypes.node.isRequired,
  editControl: PropTypes.node.isRequired,
  handleEditControl: PropTypes.func.isRequired,
  editOptionsValue: PropTypes.node.isRequired,
  handleEditOptionsValue: PropTypes.func.isRequired,
};
