import PropTypes from 'prop-types';
import Typography from '@mui/material/Typography';
import SaveIcon from '@mui/icons-material/Save';
import DownloadIcon from '@mui/icons-material/Download';
import PrintIcon from '@mui/icons-material/Print';
import List from '@mui/material/List';
// import { alpha } from '@mui/material/styles';

import DBNA from 'components/collage/DesktopActionItem';
import DesktopDrawer from 'components/collage/DesktopDrawer';
import ControlsDivider from 'components/collage/ControlsDivider';

export default function DesktopActions({ actionsRef, onDownload, onSave }) {
  const handleChange = (newValue) => {
    if (newValue === 'download') {
      onDownload();
    }
    if (newValue === 'save') {
      onSave();
    } else {
      console.log('functionality for ', newValue, 'not yet implemented');
    }
  };

  return (
    <DesktopDrawer anchor="right" innerRef={actionsRef}>
      <List sx={{ backgroundColor: 'black' }}>
        <DBNA
          label="Save"
          icon={<SaveIcon />}
          handleChange={() => handleChange('save')}
        />
        <ControlsDivider />
        <DBNA
          label="Download"
          icon={<DownloadIcon />}
          handleChange={() => handleChange('download')}
        />
        <ControlsDivider />
        <DBNA
          label={
            <>
              <Typography sx={{ fontSize: '0.75rem', fontWeight: 'bold' }}>
                Order
              </Typography>
              <Typography sx={{ fontSize: '0.75rem', fontWeight: 'bold' }}>
                Print(s)
              </Typography>
            </>
          }
          icon={<PrintIcon />}
          handleChange={() => handleChange('print')}
        />
      </List>
    </DesktopDrawer>
  );
}

DesktopActions.propTypes = {
  actionsRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({ current: PropTypes.instanceOf(Element) }),
  ]).isRequired,
  onDownload: PropTypes.func.isRequired,
  onSave: PropTypes.func.isRequired,
};
